@font-face {
  font-family: 'Montserrat-Black';
  font-style: normal;
  src: url('../public/fonts/Montserrat-Black.ttf') format('opentype');
}
@font-face {
  font-family: 'Pretendard-Black';
  font-style: normal;
  font-weight: normal;
  src: url('../public/fonts/Pretendard-Black.woff2') format('woff2');
}
@font-face {
  font-family: 'Pretendard-Regular';
  font-style: normal;
  font-weight: normal;
  src: url('../public/fonts/Pretendard-Regular.woff2') format('woff2');
}
html,
body {
  cursor: url('../public/images/cursor.png'), auto;
}

.cursor-pointer {
  cursor: url('../public/images/cursor_pointer.png'), pointer;
}
